
// 导入vue的api
import { defineComponent } from 'vue'
export default defineComponent({
  name: 'ValidateForm',
  emits: ['form-validate'],
  setup (props, context) {
    // return setup compsition Api
    const onFormSubmit = (x: boolean) => {
      context.emit('form-validate', x)
    }
    return {
      onFormSubmit
    }
  }
})
